* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  font-family: initial;
}
.light {
  --main-color: #1c1d25;
  --main-background-color: #ebebeb;
  --secondary-background-color: white;
  --filter: blur(200px);
  --filterIcons: none;
  --secondary-color: rgb(25, 150, 161);
}
.dark {
  --main-color: white;
  --main-background-color: #1c1d25;
  --secondary-background-color: rgba(0, 0, 0, 0.15);
  --secondary-color: #eeef58;
  --filter: blur(100px);
  --filterIcons: invert(1);
}

body {
  margin: 16px;
  font-size: 100%;
  font-family: "Open Sans", sans-serif;
  color: var(--main-color);
  height: 100%;
  background: var(--main-background-color);
  background-repeat: no-repeat;
  background-attachment: fixed;
}
nav {
  display: flex;
  width: 100%;
  height: 80px;

  justify-content: center;
  position: relative;
}
.linksNav {
  display: flex;
  font-size: 1.5em;
}
.linksNav div {
  margin-left: 5px;
  margin-right: 5px;
}
nav a {
  color: var(--main-color);
  text-decoration: none;
}

.main-container {
  display: block;
  flex-wrap: wrap;
}

.location-and-date {
  width: 100%;
  padding: 1em;
  background-color: var(--secondary-background-color);
  border-radius: 20px;
  color: var(--secondary-color);
  margin-bottom: 1em;
}

.location-and-date__location {
  margin: 0;
  font-size: 2em;
  font-weight: 600;
}

.current-temperature {
  display: flex;
  margin-top: 0.25em;
  width: 100%;
  padding: 1em;
  color: var(--secondary-color);
}

.current-temperature__icon-container {
  flex-grow: 1.25;
  text-align: center;
}

.current-temperature__content-container {
  flex-grow: 1;
  text-align: center;
}

.current-temperature__icon {
  width: 10.5em;
}

.current-temperature__value {
  font-size: 5.25em;
  font-weight: 300;
}

.current-temperature__summary {
  margin-top: -0.5em;
  margin-left: -0.6em;
  font-size: 1.125em;
}

.current-stats {
  display: flex;
  justify-content: space-around;
  padding: 1em;
  width: 100%;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  color: var(--secondary-color);
}
.weather-by-hour__item {
  padding: 0.8em 0;
  margin: 0.5em;
  width: 13%;
  border-radius: 5px;
  background-color: var(--secondary-background-color);
  font-size: 1.125em;
  text-align: center;
  border-radius: 20px;
}

.weather-by-hour {
  display: none;
  width: 100%;
}

.next-5-days {
  width: 100%;
  margin-top: 1em;
}

.next-5-days__container {
  display: flex;
  flex-wrap: wrap;
}

.next-5-days__row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 0.3em;
  padding: 0.8em 0;
  width: 100%;
  border-radius: 5px;
  background-color: var(--secondary-background-color);
  font-size: 1.19em;
  text-align: center;
  border-radius: 20px;
}

.next-5-days__label {
  color: var(--main-color);
  font-size: 0.83em;
}

.next-5-days__date,
.next-5-days__high,
.next-5-days__low,
.next-5-days__icon,
.next-5-days__rain,
.next-5-days__wind {
  width: 33.33333%;
  font-size: 0.95em;
}

.next-5-days__date,
.next-5-days__high,
.next-5-days__low {
  margin-bottom: 0.6em;
}

/************************** TABLET ***************************/
@media screen and (min-width: 768px) {
  .weather-by-hour {
    display: block;
  }

  .current-stats {
    margin-bottom: 3em;
    padding: 0;
    padding-bottom: 1em;
    border-top: none;
    border-bottom: none;
    border-left: 1px solid rgba(255, 255, 255, 0.5);
  }
  .weather-by-hour__container {
    display: flex;
    justify-content: space-between;
  }

  .weather-by-hour__heading,
  .next-5-days__heading {
    color: var(--main-color);
    font-size: 1.5em;
    font-weight: normal;
  }

  .next-5-days__date,
  .next-5-days__high,
  .next-5-days__low,
  .next-5-days__icon,
  .next-5-days__rain,
  .next-5-days__wind {
    width: 16.666666%;
    margin-bottom: initial;
  }

  .next-5-days__date {
    order: -2;
  }

  .next-5-days__icon {
    order: -1;
  }

  .current-stats__value {
    margin-top: 1em;
    font-size: 1.44em;
  }

  .current-stats__label {
    color: var(--main-color);
  }
  .current-temperature {
    width: 35%;
  }
  .location-and-date {
    padding: 3em;
  }
}

/*********************** DESKTOP *********************/
@media screen and (min-width: 1000px) {
  .main-container {
    display: flex;
    width: 980px;
    margin: 16px auto;
    font-size: 1.1em;
  }
  .current-stats {
    width: 60%;
    border-left: 1px solid var(--secondary-color);
    margin-top: 0.25em;
  }
  .weather-by-hour__hour {
    margin-bottom: 0.5em;
  }
}

form {
  display: inline-grid;
}
.form-input {
  color: var(--main-color);
  position: relative;
  top: 110px;
  height: 25px;
  width: 200px;
  margin: 10px;
  border: none;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid var(--secondary-color);
  transition: all 0.5s;
  padding: 1em;
}

input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
  background-color: var(--main-color);
  color: var(--main-color);
}

.submit {
  position: relative;
  top: 160px;
  width: 160px;
  height: 30px;
  border: none;
  border-radius: 20px;
  background-color: var(--secondary-color);
  color: black;
  font-size: 15px;
  font-weight: bold;
  transition: all 0.5s;
  margin: 0 auto;
}

::placeholder {
  color: var(--main-color);
}
svg {
  color: white;
}
.weatherIcon {
  width: 50%;
  filter: var(--filterIcons);
}
/*////////////////Toggle///////////////////////*/
.theme-toggle {
  --size: 25px;
  position: relative;
  width: calc(var(--size) * 2);
  height: var(--size);
  background-color: var(--main-color);
  border-radius: 15px;
  position: absolute;
  right: 0;
}

.theme-toggle label {
  position: absolute;
  top: 50%;
  left: -2%;
  transform: translateY(-52%);
  width: var(--size);
  height: var(--size);
  background-color: #eeef58;
  border-radius: 50%;
  transition: 150ms left;
  cursor: pointer;
  border: 2px solid #eeef58;
  z-index: 1;
}

input[type="checkbox"]:checked ~ label {
  left: calc(100% - var(--size));
}

.theme-toggle input[type="checkbox"] {
  display: none;
}

.theme-toggle span {
  position: absolute;
  top: 0;
  left: calc(100% - 23px);
}

.theme-toggle .on {
  left: 2.5px;
}

input[type="checkbox"]:checked ~ .on {
  display: block;
}
#mobile-menu {
  display: none;
}
.hidden {
  display: none !important;
}
